<template>
    <el-container class="home-container">
        <el-header>
           <div>
            <img src="../assets/heima.png" title="">
            <span>电商后台管理系统</span>
           </div>
           <el-button type="info" @click="logout">退出</el-button>
        </el-header>
        <el-container>
            <!-- 侧边栏区域  -->
            <!-- isCollapse 是true 折叠状态 侧边栏宽度为icon图标的宽度 否则侧边栏宽度为默认值200px  -->
            <el-aside :width="isCollapse?'64px':'200px'">
            <div class="toggle-button" @click="toggleCollapse">|||</div>
            <!-- 菜单栏   保持只有一个打开状态 即一个为展开的情况下，其他均为折叠状态-->
            <!--router属性可以开启侧边栏的路由模式 取值为boolean 但是跳转地址是index凭借的字符串 所以要对index的字符串进行改造 -->
            <el-menu background-color="#545c64" text-color="#fff" active-text-color="#409eff" unique-opened  :collapse="isCollapse"  :collapse-transition="false" router :default-active="activePath"  >
            <!-- 一级菜单 -->
            <el-submenu  v-for="item in menulist" :key="item.id" :index="item.id+''">
                <!-- 一级菜单的模板区域 -->
                <template slot="title" >
                <!-- i是图标 span是菜单名 -->
                <i :class="iconObj[item.id]"></i>
                <span>{{ item.authName }}</span> 
              </template>
             <!-- 二级菜单  --> 
                <el-menu-item  v-for="subItem in item.children" :key="subItem.id" :index="'/'+subItem.path"  @click="saveNavStatus('/'+subItem.path)" >
                    <!-- :default-active="activePath"   @click="saveNavStatus('/'+subItem.path)" -->
                    <template slot="title">
                        <!-- i是图标 span是菜单名 -->
                        <i class="el-icon-menu"></i>
                        <span>{{ subItem.authName }}</span>
                      </template>
                </el-menu-item>
            </el-submenu>
          
          </el-menu>
          </el-aside>
          <el-main>
            <!-- 在el-main中留下占位符 -->
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
        

</template>
<script>
export default {
    data(){
        return {
            menulist:[],
            iconObj:{
                '125':'iconfont icon-user',
                '103':'iconfont icon-tijikongjian',
                '101':'iconfont icon-shangpin',
                '102':'iconfont icon-danju',
                '145':'iconfont icon-baobiao',
            },
            isCollapse:false,
            activePath:''
        }
    },
    // 被创建时获取左侧菜单
    created(){
        this.getMenuList()
        this.activePath=window.sessionStorage.getItem('activePath')
    },
    methods:{
        logout(){
            window.sessionStorage.clear()
            this.$router.push('/login')
        },
        async getMenuList(){
            const {data:res} = await this.$http.get('menus');
            if(res.meta.status!=200) return this.$message.error(res.meta.msg)
            this.menulist=res.data
     
        }  ,
        toggleCollapse(){
            this.isCollapse=!this.isCollapse
        },
        saveNavStatus(activePath){
            window.sessionStorage.setItem('activePath',activePath)
            this.activePath=activePath
        }
}
}
</script>

<style lang="less" scoped>
.home-container{
    height: 100%;
}
.el-header{
    background-color: #363D40;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    padding-left:0px ;
    color: #fff;
    >div{
        display: flex;
        align-items: center;
        span{
        padding-left: 15px;

        }
    }
}
.el-aside{
    background-color: #313743;
    //右侧边框线突出
    .el-menu{
        border-right: none;
    }
}
.el-main{
    background-color: #E9EDF1;
}
.iconfont{
    margin-right: 10px;
}
.toggle-button{
     background-color: #313743;
     color: #fff;
     font-size: 10px;
     letter-spacing:0.2em;
     cursor: pointer;
     line-height: 24px;
     text-align: center ;
}
</style>